        // 取 API 回傳的音軌毫秒進行換算
        if (typeof res.duration_ms === "number") {
          /** 分鐘數 */
          const minutes = Math.floor(res.duration_ms / 1000 / 60);
          /** 初步計算的秒數值 */
          let secondValue = Math.floor((res.duration_ms / 1000) % 60);
          /** 秒數 */
          const seconds =
            secondValue.toString().length === 1
              ? `0${secondValue}`
              : secondValue;
          res.duration_ms = `${minutes}:${seconds}`;
        }
      });
    }
  } else {
    isLogin.value = false;
  }
}

